.slider {
    position: relative;
    margin-bottom: 7vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

.image-gallery {
  transition: all 0.3s ease-in-out !important;
}

.image-gallery:hover {
  transform: scale(1.1) !important;
  transition: all 0.3s ease-in-out !important;
}

  body {
    background-color: #fdfdfd;
    overflow-x: hidden;
    width: 100% !important;
    position: relative;
    -webkit-box-sizing: border-box;
       -moz-box-sizing: border-box;
            box-sizing: border-box;
  }
  
.categories { 
  width: 100%;
}

.categories-more {
  display: flex;
  cursor: pointer;
  font-family: Roboto;
  font-weight: 400;
  color: #e7e7e7;
  background-color: #0f0f0f;
  padding: 12px;
  margin-left: 45vw;
  margin-right: 45vw;
  border-radius: 4px;
  text-decoration: none;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  transition: all 0.19s cubic-bezier(.66,.01,.36,.99);
}

.categories-more:hover, .categories-more:focus-visible {
  color: #0f0f0f;
  background-color: #ffffff;
  margin-left: 0;
  margin-right: 0;
}

.card {
  transition: all 0.20s ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: #181818 0px 11px 20px;
}

  .slider-image {
    max-width: 1000px;
    width: 100%;
    max-height: 600px;
    height: auto;
    border-radius: 4px;
  }
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: #000000;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }

  .left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: #000000;
    z-index: 10;
    cursor: pointer;
    user-select: none;
  }

  .slide {
    opacity: 0;
    transition-duration: 1s linear;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
  }

 .copyright {
  display: flex;
  justify-content: center;
  color: #444343;
  font-weight: 100;
  font-size: calc(5px + 6 * ((100vw - 320px) / 680));
  margin: 35px;
  margin-top: 40px;
  margin-bottom: 20px;
 }

 @media (min-width: 100px) and (max-width: 600px) {
  .categories-more {
    margin-left: 10vw;
    margin-right: 10vw;
  }
  .left-arrow, .right-arrow {
    color: rgb(102, 102, 102);
    border-radius: 100%;
  }
}

.menuBackground {
  background-color: transparent !important;
  box-shadow: none !important;
  animation-name: fademenu;
  animation-duration: 4.5s;
}

@keyframes fademenu {
  0% {
  opacity: 0
  }
  100% {
  opacity: 1
  }
}